<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @focus="supplierListInvok" @change="changeSuppliter" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="商品名称" size="mini">
          <el-input v-model="params.goods_name" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="胎号" size="mini">
          <el-input v-model="params.tire_un" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="厂家理赔单据号" size="mini">
          <el-input v-model="params.factory_claim_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="审核状态" size="mini">
          <el-select v-model="params.auditor_status" clearable>
            <el-option label="全部" :value="0">全部</el-option>
            <el-option label="未审核" :value="1">未审核</el-option>
            <el-option label="已审核" :value="2">已审核</el-option>
            <el-option label="作废" :value="3">作废</el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          <el-select clearable v-model="params.department_id" @change="departmentChange" @focus="DepartmentListInvok" placeholder="请选择" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addBtn" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      <el-button @click="examineFun" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
      <el-button plain @click="examineFunF" icon="iconfont icon-fanshenhe" class="addBtn" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
      <el-button plain @click="abolishFun" size="mini" icon="iconfont icon-zuofei" v-if="authority.b_invalid==1">作废</el-button>
      <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column label="操作" fixed="right" width="80">
          <template slot-scope="scope">
            <el-button @click="handleEditSearch(scope, 1)" type="text" size="small">
              查看
            </el-button>
            <el-button @click="handleEditSearch(scope, 2)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">
              修改
            </el-button>
            <el-button @click="handleEditSearch(scope, 2)" disabled type="text" size="small" v-else>
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="200" label="销售组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_claim_sn" width="200" label="厂家理陪单据号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="200" label="供应商(厂家)"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" width="100" label="品牌名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_sn" width="200" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" width="120" label="胎号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="symptom_name" width="120" label="病象"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="charge_flowerdeep" width="120" align="right" label="收费花深"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" width="200" align="right" label="商品采购价"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="wear_price" width="120" align="right" label="磨损费"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="inflap_price" width="120" align="right" label="内垫采购价"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_price" width="120" align="right" label="厂赔单价"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="120" label="操作员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="单据时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="120" label="审核状态"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_appraisal_sn" width="180" label="厂检单号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="150" label="审核时间">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" width="200" label="备注"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <!-- 弹窗- 修改/查看 -->
    <div class="claims_dialog">
      <el-dialog :close-on-click-modal="false" :title="dialogTitle" :visible.sync="dialogIsShow" width="900px" center>
        <div v-loading="dialogLoading">
          <dialog-content ref="dialogContent" :title_name="title_name" :dialog-params="dialogParams" :is_search="is_search"></dialog-content>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogIsShow = false">关闭</el-button>
          <el-button type="primary" :disabled="dialogLoading" v-if="!is_search" @click="editeSubmit()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import dialogContent from "./manufacturersclaim_dialog";
export default {
  name: "aftersaleManufacturersclaimDefault",
  data() {
    return {
      timeValue1: [],
      params: {
        auditor_status: 0
      },
      is_search: false, // 修改/查看
      title_name: "", // 厂家理赔单title
      dialogTitle: "修改厂家理赔单", // dialog弹框title
      dialogIsShow: false, // 控制dialog弹框是否显示
      dialogLoading: false, // 弹框中的加载
      dialogParams: {
        factory_claim_id: "", // 单据Id
        factory_claim_goods_tire_id: "", // 单据胎号主键
        company_name: "", // 组织名称
        department_name: "" // 部门名称
      } // 弹框中内容
    };
  },
  components: {
    dialogContent
  },
  computed: {
    ...mapGetters(["manufacturersclaimDefault_Get"])
  },
  methods: {
    ...mapActions(["manufacturersclaimAdd", "manufacturersclaimDefault"]),
    // 接口 获取厂家理赔单详情
    portGetFactoryClaimInfo(id) {
      this.dialogLoading = true;
      this.$axMethod
        .getFactoryClaimInfo({
          factory_claim_goods_tire_id: id
        })
        .then(({ data }) => {
          this.dialogLoading = false;
          if (data.code == 200) {
            let temp = data.data;
            if (temp["factory_status"] == 0) {
              temp["factory_status"] = 1;
            }
            Object.keys(temp).forEach(key => {
              this.$set(this.dialogParams, key, temp[key]);
            });
          }
        })
        .catch(error => {
          this.dialogLoading = false;
        });
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let instaidarry = this.multipleSelection.map(item => {
        return item.factory_claim_goods_tire_id;
      });
      Object.assign(this.params, {
        export_type: scheduleType,
        id_arr: instaidarry.toString()
      });
      this.$axMethod.aftersaleExportFactoryClaimUrl(this.params).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    // 处理 字段对象 json_info 中使用
    dealJsonInfo(item, fieldObj) {
      let newObj = {};
      if (Object.keys(fieldObj).length > 0) {
        for (let key in fieldObj) {
          // newObj 中和 送字段统计的字段
          if (!item[key] && item[key] != 0) {
            // 判断值为undefined null
            newObj[key] = fieldObj[key];
          } else {
            newObj[key] = item[key];
          }
        }
      }
      return newObj;
    },
    // 接口 修改厂家理赔单
    portUpdateFactoryClaim(params) {
      this.dialogLoading = true;
      this.$axMethod
        .updateFactoryClaim(params)
        .then(({ data }) => {
          this.dialogLoading = false;
          if (data.code == 200) {
            this.dialogIsShow = false; // 关闭弹框
            this.$message({
              message: data.msg || data.data,
              type: "success"
            });
            // 重新请求接口
            this.onSearch();
          }
        })
        .catch(error => {
          this.dialogLoading = false;
        });
    },
    // 单据修改 提交
    editeSubmit() {
      let dialogContent = this.$refs.dialogContent;
      if (!dialogContent) return;
      dialogContent.$refs.dialogFrom.validate(valid => {
        if (valid) {
          let params = {
              factory_claim_id: this.dialogParams.factory_claim_id,
              factory_claim_goods_tire_id: this.dialogParams
                .factory_claim_goods_tire_id
            },
            editField = {
              factory_status: 1, // 厂家理赔
              raw_flowerdeep: 0, // 出厂花深
              surplus_flowerdeep: 0, // 剩余花深
              factory_flowerdeep: 0, // 厂免花深
              charge_flowerdeep: 0, // 收费花深
              mm_wearprice: 0, // 毫米磨损费
              wear_price: 0, // 磨损费
              intube_price: 0, // 内胎采购价
              flap_price: 0, // 垫带采购价
              inflap_price: 0, // 内垫采购价
              purchase_price: 0, // 商品采购价
              factory_price: 0, // 厂赔单价
              remark: "" // 备注
            };
          params.json_info = JSON.stringify(
            this.dealJsonInfo(this.dialogParams, editField)
          );
          this.portUpdateFactoryClaim(params);
        }
      });
    },

    // 点击查看
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      this.manufacturersclaimDefault(this.searchParams);
      getData(this.searchParams);
      this.manufacturersclaimDefault({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    supplierChange(val) {
      // let options = {
      //   company_id: this.params.company_id,
      //   supplier_name: val
      // };
      // supplierListOpen(this, options);
      this.supplierListInvok(val);
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_claim_goods_tire_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "factory_claim_goods_tire_id",
        eachData: {
          factory_claim_goods_tire_id: rebateArr
        },
        examineApi: "aftersaleAuditorPass",
        list: {
          UrlApi: "getManufactorClaimList",
          data: this.TableData
        }
      };
      this.$common.examineFun(funcan);
    },
    examineFunF() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_claim_goods_tire_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "factory_claim_goods_tire_id",
        eachData: {
          factory_claim_goods_tire_id: rebateArr
        },
        examineApi: "aftersaleAuditorNoPass",
        list: {
          UrlApi: "getManufactorClaimList",
          data: this.TableData
        }
      };
      this.$common.examineFun(funcan);
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_claim_goods_tire_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "factory_claim_goods_tire_id",
        eachData: {
          factory_claim_goods_tire_id: rebateArr
        },
        examineApi: "aftersaleAuditorInvalid",
        list: {
          UrlApi: "getManufactorClaimList",
          data: this.TableData
        }
      };
      this.$common.examineFun(funcan);
    },
    onReset() {
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "searchParams", {
        params: {
          auditor_status: 0
        },
        TableData: this.TableData, //列表数据
        API: "getManufactorClaimList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      this.manufacturersclaimDefault({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      // getDepartmentListCommon(this, this.params, 1);
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    changeSuppliter(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    addBtn() {
      //新增
      this.manufacturersclaimAdd({
        // moduleName: "manufacturersclaimAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/manufacturersclaim/manufacturersclaim_add"
      });
    },
    // 修改/查看 操作 type 1 查看 2 修改
    handleEditSearch(scope, type) {
      let { row } = scope;
      this.title_name = `厂家理赔单据号 ${row.factory_claim_sn}`; // 赋值单据号
      Object.keys(this.dialogParams).forEach(key => {
        this.dialogParams[key] = row[key];
      });
      this.portGetFactoryClaimInfo(row.factory_claim_goods_tire_id);
      switch (type) {
        case 1: // 查看
          this.dialogTitle = "查看厂家理赔单";
          this.is_search = true; // 禁止编辑
          break;
        case 2: // 修改
          this.dialogTitle = "修改厂家理赔单";
          this.is_search = false; // 允许编辑
          break;
          break;
      }
      this.dialogIsShow = true; // 打开弹框
      this.$nextTick(() => {
        // 移除校验结果
        let dialogContent = this.$refs.dialogContent;
        if (dialogContent) {
          dialogContent.$refs.dialogFrom.clearValidate();
        }
      });
      // 2019/11/28 1304需求弃用
      // this.manufacturersclaimAdd({
      //   // moduleName: "manufacturersclaimMofify",
      //   factory_claim_goods_tire_id: row.factory_claim_goods_tire_id,
      //   params: this.params,
      //   goPage: this.goPage,
      //   pageSize: this.pageSize
      // });
      // this.$router.push({
      //   path: "/aftersale/manufacturersclaim/manufacturersclaim_modify"
      // });
    },
    deletes() {
      //删除
      var deledata = {
        deleID: "factory_claim_goods_tire_id",
        deleSelect: this.multipleSelection,
        deleApi: "aftersaleDelManufactorClaim",
        list: {
          UrlApi: "getManufactorClaimList",
          data: this.TableData,
          esData: {
            page: this.goPage
          }
        }
      };
      this.$common.deletes(deledata, "删除", this);
    }
  },
  mounted() {
    // 初始值
    this.params = Object.assign(this.params, {
      auditor_status: 0 // 审核状态
    });
    let wz_search_params = {};
    if (
      this.manufacturersclaimDefault_Get &&
      this.manufacturersclaimDefault_Get.params
    ) {
      if (this.manufacturersclaimDefault_Get.params.start_time) {
        const {
          start_time,
          end_time
        } = this.manufacturersclaimDefault_Get.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.manufacturersclaimDefault_Get.params.auditor_start_time) {
        const {
          auditor_start_time,
          auditor_end_time
        } = this.manufacturersclaimDefault_Get.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time
        ]);
      }
      this.$set(this, "params", this.manufacturersclaimDefault_Get.params);
      wz_search_params = this.params;
      opthionList(this, this.manufacturersclaimDefault_Get.params);
    }
    if (
      this.manufacturersclaimDefault_Get &&
      this.manufacturersclaimDefault_Get.goPage
    ) {
      this.params.page = this.manufacturersclaimDefault_Get.goPage;
      this.params.per_page = this.manufacturersclaimDefault_Get.pageSize;
    }
    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getManufactorClaimList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

<style lang="less" scoped>
.claims_dialog {
  /deep/ .el-dialog__body {
    padding: 0;
  }
}
</style>
